:root {
	--yellow: #ffc600;
}

html {
	color: #444;
	font-family: Cambria, Cochin, Georgia, 'Times New Roman', Times, serif;
	font-size: 1.25rem;
}

p {
	line-height: 1.8;
}

img {
	max-width: 100%;
}

a {
	color: black;
	-webkit-text-decoration: underline wavy var(--yellow);
	        text-decoration: underline wavy var(--yellow);
}

h1,
h2 {
	font-size: 5rem;
	font-style: italic;
	font-weight: 100;
	margin: 0;
}

.post {
	display: grid;
	max-width: 60rem;
	margin: 12.5rem auto;
	grid-gap: .5rem 2.5rem;
	grid-template-columns: 3fr 12fr 5fr;
}

.post > * {
	grid-column: 2 / -2;
}

.post > figure {
	margin: 0;
	grid-column: 1 / -1;
}

figcaption {
	font-size: .5rem;
}

.post > blockquote {
	grid-column: 1 / -1;
	font-size: 3.75rem;
	font-style: italic;
	text-align: center;
	margin: 0;
}

.tip {
	background: #fafafa;
	padding: .5rem;
	grid-row: span 5;
	-ms-flex-item-align: start;
	    align-self: start;
}

.tip-left {
	grid-column: 1 / span 1;
	text-align: right;
	border-right: .125rem solid var(--yellow);
}

.tip-right {
	grid-column: span 1 / -1;
	border-left: .125rem solid var(--yellow);
}



